	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" title=\":triangular_flag_on_post:\" alt=\":triangular_flag_on_post:\" class=\"forum-image\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" title=\":busts_in_silhouette:\" alt=\":busts_in_silhouette:\" class=\"forum-image\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" title=\":pencil:\" alt=\":pencil:\" class=\"forum-image\"></p>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" title=\":checkered_flag:\" alt=\":checkered_flag:\" class=\"forum-image\"> Problem Explanation:</h3>\n<p>The algorithm to convert from Celsius to Fahrenheit is the temperature in Celsius times <code>9/5</code>, plus <code>32</code>.</p>\n<p>You are given a variable <strong>celsius</strong> representing a temperature in Celsius. Use the variable <strong>fahrenheit</strong> already defined and apply the algorithm to assign it the corresponding temperature in Fahrenheit.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='http://www.purplemath.com/modules/orderops.htm' target='_blank' rel='nofollow'>The Order of Operations: PEMDAS</a></li>\n<li><a href='https://www.khanacademy.org/math/pre-algebra/order-of-operations/order_of_operations/v/order-of-operations' target='_blank' rel='nofollow'>Order of Operation: Video</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 1</h2>\n<p>Take a look at the code. There is an area that you’re not supposed to edit. From there, ask yourself - what is used there that I haven’t seen before?</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 2</h2>\n<p>Keep in mind the <strong>order of operation</strong> check the link in the <em>links</em> section for more information.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"warning sign\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" title=\":beginner:\" alt=\":beginner:\" class=\"forum-image\"> Basic Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>    <span class=\"token keyword\">function</span> <span class=\"token function\">convertToF</span><span class=\"token punctuation\">(</span>celsius<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token comment\" spellcheck=\"true\">// Only change code below this line</span>\n      <span class=\"token keyword\">var</span> fahrenheit <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>celsius <span class=\"token operator\">*</span> <span class=\"token punctuation\">(</span><span class=\"token number\">9</span><span class=\"token operator\">/</span><span class=\"token number\">5</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> <span class=\"token number\">32</span><span class=\"token punctuation\">;</span>\n\n      <span class=\"token comment\" spellcheck=\"true\">// Only change code above this line</span>\n      <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span> <span class=\"token keyword\">typeof</span> fahrenheit <span class=\"token operator\">!==</span> <span class=\"token string\">'undefined'</span> <span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">return</span> fahrenheit<span class=\"token punctuation\">;</span>\n      <span class=\"token punctuation\">}</span> <span class=\"token keyword\">else</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">return</span> <span class=\"token string\">'fahrenheit not defined'</span><span class=\"token punctuation\">;</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\" spellcheck=\"true\">// Change the inputs below to test your code</span>\n    <span class=\"token function\">convertToF</span><span class=\"token punctuation\">(</span><span class=\"token number\">30</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Code Explanation:</h3>\n<ul>\n<li>Declare the <strong>fahrenheit</strong> variable.</li>\n<li>Make sure the proper order of arithmetic operations is followed by using parenthesis (<code>()</code>) when needed.</li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" title=\":clipboard:\" alt=\":clipboard:\" class=\"forum-image\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories — <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" title=\":traffic_light:\" alt=\":traffic_light:\" class=\"forum-image\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" title=\":point_right:\" alt=\":point_right:\" class=\"forum-image\"> <a href='http://forum.freecodecamp.com/t/algorithm-article-template/14272' target='_blank' rel='nofollow'><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>","fields":{"slug":"/certificates/convert-celsius-to-fahrenheit/"},"frontmatter":{"title":"Convert Celsius to Fahrenheit"}}},"pathContext":{"slug":"/certificates/convert-celsius-to-fahrenheit/"}}

